import axios from 'axios';
import { CALCULATED_CART } from './urls';

export const fetchCalculatedCart = (cartItems, successCallback, errorCallback) => {
    axios.post(
        CALCULATED_CART,
        {
            products: cartItems
        }
    )
        .then(res => {
            successCallback(res);
        })
        .catch(err => {
            errorCallback(err);
        });
};
